import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "latex",
})
export class LatexPipe implements PipeTransform {
  //   Ä (hold down the Alt key and type 0196)
  // ä (Alt 0228)
  // Ö (Alt 0214)
  // ö (Alt 0246)
  // Ü (Alt 0220)
  // ü (Alt 0252)
  // ß (Alt 0223)
  transform(value: string, args?: any): string {
    // let regex = new RegExp(find, "gm");
    if (value === undefined) {
      return "";
    }

    value = value.replace(new RegExp('{\\"{A}}|\\"{A}', "gm"), "Ä");
    value = value.replace(new RegExp('{\\"{a}|\\"{a}|�|\\"a', "gm"), "ä");

    value = value.replace(new RegExp('\\"{O}|{\\"{O}}', "gm"), "Ö");
    value = value.replace(new RegExp('{\\"{o}}|\\"{o}|�|\\"o', "gm"), "ö");

    value = value.replace(new RegExp('\\"{U}|{\\"{U}}', "gm"), "Ü");
    value = value.replace(new RegExp('{\\"{u}}|\\"{u}|\\"u', "gm"), "ü");

    value = value.replace(new RegExp("{\\'e}|\\'e|{'e}", "gm"), "é");

    value = value.replace(new RegExp("&", "gm"), "&");

    value = value.replace(new RegExp("{|}", "gm"), "");

    value = value.replace(new RegExp("{-}", "gm"), "-");

    // return value.replace(new RegExp("\\", "gm"), "");
    return value;
  }
}



